import React, {useState, useEffect} from "react";
import {Modal, Table} from "react-bootstrap";
import axios from "axios";

import {errorToast, getCookieValue, ipAddress, tokenName} from "../../../../utils/Common";
import Button from "react-bootstrap/Button";

const ViewAirlines = () => {
  const tableHeadings = ["Name", "Code", "Type", "ActionBy", "ActionOn", "Edit Account Code", "Edit", "Delete", "Details"];

  const [airlines, setAirlines] = useState(null);

  async function FetchAirlines() {
    try {
      const token = getCookieValue(tokenName);

      // const url = `${ipAddress}admin/airlines`
      const url = `${ipAddress}admin/airlines-new`

      const result = await axios.get(url, {
        headers: {'Authorization': `Bearer ${token}`}
      })
      // console.log(result)
      // console.log(result.status)

      if (result.status === 200) {
        const {error, message, recordset} = result.data

        if (error) {
          errorToast(message)
        } else {
          // console.log(result.data.recordset)
          setAirlines(result.data.recordset)
        }
      }
    } catch (e) {
      console.log(e)
      errorToast(e.message)
    }
  }

  useEffect(() => {
    FetchAirlines().then()
  }, []);

  function formatDate(dateString) {
    // Create a new Date object from the input string
    const date = new Date(dateString);

    // Extract day, month, and year
    const day = date.getUTCDate();
    const month = date.toLocaleString('en-GB', {month: 'short'});
    const year = date.getUTCFullYear();

    // Extract hours and minutes
    const hours = String(date.getUTCHours()).padStart(2, '0');
    const minutes = String(date.getUTCMinutes()).padStart(2, '0');

    // Format the date as "24 Apr 2023 22:28"
    return `${day} ${month} ${year} ${hours}:${minutes}`;
  }

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [modalData, setModalData] = useState(null);

  function ShowAirlinesDetailsInModal(airline) {
    // console.log(airline)
    setModalData(airline)
    handleShow()
  }

  return (
    <>
      <div style={{height: "330px"}} className="table-responsive">
        <Table responsive hover>
          <thead style={{position: "sticky", top: 0}}>
          <tr>
            {tableHeadings.map((x, i) => <th key={i}>{x}</th>)}
          </tr>
          </thead>
          <tbody>
          {airlines ? airlines.length > 0 &&
            airlines.map(x =>
              <tr key={x.AirlineIndex}>
                <td>{x.Name}</td>
                <td>{x.Code}</td>
                <td>{x.Type}</td>
                <td>{x.ActionBy}</td>
                {/*<td>{x.ActionOn}</td>*/}
                <td>{formatDate(x.ActionOn)}</td>
                <td>
                  <button className="btn btn-sm btn-primary btn-link">Edit Account Code</button>
                </td>
                <td>
                  <button className="btn btn-sm btn-primary btn-link">Edit</button>
                </td>
                <td>
                  <button className="btn btn-sm btn-primary btn-link">Delete</button>
                </td>
                <td>
                  <button onClick={() => ShowAirlinesDetailsInModal(x)} className="btn btn-sm btn-primary btn-link">
                    Details
                  </button>
                </td>
              </tr>
            ) : <tr>
            <td colSpan={9} className="text-center">
              <i className="spinner spinner-border"></i>
            </td>
          </tr>
          }
          </tbody>
        </Table>
      </div>

      <Modal show={show} onHide={handleClose} centered size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Details Airline</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="col-md-4 mb-3">
              <p><b>Name</b>:<br/>{modalData?.Name}</p>
            </div>

            <div className="col-md-4 mb-3">
              <p><b>Code</b>:<br/>{modalData?.Code}</p>
            </div>

            <div className="col-md-4 mb-3">
              <p><b>Account Code</b>:</p>
            </div>

            <div className="col-md-4 mb-3">
              <p><b>Type</b>:<br/>{modalData?.Type}</p>
            </div>

            <div className="col-md-4 mb-3">
              <p><b>ActionBy</b>:<br/>{modalData?.ActionBy}</p>
            </div>

            <div className="col-md-4 mb-3">
              <p><b>ActionOn</b>:<br/>{formatDate(modalData?.ActionOn)}</p>
            </div>
          </div>

          <div className="text-end">
          <Button variant="secondary" onClick={handleClose} className="px-5">
              Close
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  )
}
export default ViewAirlines;
