import React from "react";
import axios from "axios";
import {useForm} from "react-hook-form";
import Button from "react-bootstrap/Button";
import {errorToast, getCookieValue, ipAddress, tokenName} from "../../../../utils/Common";

const CreateAirline = ({handleClose}) => {
  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm();

  async function onSubmit(data) {
    try {
      // console.log(data)
      // console.log(data.file[0])

      const token = getCookieValue(tokenName);

      const result = await axios.post(ipAddress + "admin/airlines", data, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        }
      })
      console.log(result)
    } catch (e) {
      console.log(e)
      errorToast(e.message)
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      {/* Name */}
      <div className="mb-3">
        <label htmlFor="name">Name <span className="text-danger">*</span></label>
        <input type="text" id="name" name="name" className="form-control"
               {...register('name', {
                 required: "This is a required field.",
                 validate: value => !/<|>/.test(value) || 'Name cannot contain "<" or ">"'
               })}
        />

        {errors?.name && <p className='text-danger'>{errors?.name?.message}</p>}
      </div>

      {/* Code */}
      <div className="mb-3">
        <label htmlFor="code">Code <span className="text-danger">*</span></label>
        <input type="text" id="code" name="code" className="form-control"
               {...register('code', {
                 required: "This is a required field.",
                 validate: value => !/<|>/.test(value) || 'Code cannot contain "<" or ">"'
               })}/>

        {errors?.code && <p className='text-danger'>{errors?.code?.message}</p>}
      </div>

      {/* File */}
      <div className="mb-3">
        <label htmlFor="name">File <span className="text-danger">*</span></label>
        <input type="file" accept=".jpg, .jpeg, .png"
               {...register('file', {required: "This is a required field."})}
               id="file" name="file" className="form-control"/>

        {errors?.file && <p className='text-danger'>{errors?.file?.message}</p>}
      </div>

      {/* Type */}
      <div className="mb-3">
        <label htmlFor="type">Type <span className="text-danger">*</span></label>
        <select name="type" id="type" className="form-select"
                {...register('type', {
                  required: "This is a required field.",
                  validate: value => !/<|>/.test(value) || 'Code cannot contain "<" or ">"'
                })}>
          <option value="">--Select Type--</option>
          <option value="DOM">Domestic</option>
          <option value="INT">International</option>
        </select>

        {errors?.type && <p className='text-danger'>{errors?.type?.message}</p>}
      </div>

      <div className="mb d-flex justify-content-between align-items-center">
        <Button variant="primary" type="submit" className="px-5">
          Create
        </Button>

        <Button variant="secondary" onClick={handleClose} className="px-5">
          Close
        </Button>
      </div>
    </form>
  )
}
export default CreateAirline;
