import React, {useState} from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import {CCardHeader} from '@coreui/react'
import CreateAirline from "./forms/CreateAirline";
import ViewAirlines from "./table/ViewAirlines";

const ManageAirlines = () => {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <CCardHeader><strong>Manage Airlines</strong></CCardHeader>

      <Button variant="primary" onClick={handleShow} className="mt-3 mb-4">
        Create New Airline
      </Button>

      <ViewAirlines/>

      <Modal show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Create New Airline</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <CreateAirline handleClose={handleClose}/>
        </Modal.Body>
      </Modal>
    </>
  )
}
export default ManageAirlines;
